@charset "UTF-8";
:root {
  --font-haas-display: "neue-haas-grotesk-display", sans-serif;
  --font-haas-text: "neue-haas-grotesk-text", sans-serif;
  --font-brokenplane: "Broken Planewing";
}

:root {
  --black: rgb(0, 0, 0);
  --white: rgb(255, 255, 255);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-haas-display);
  font-style: normal;
  color: var(--white);
  font-size: 0.8rem;
  font-weight: 600;
  line-height: 1.3;
  letter-spacing: 1px;
}

button {
  background: none;
  color: inherit;
  border: none;
  padding: 0;
  font: inherit;
  cursor: pointer;
  outline: inherit;
}

ul {
  list-style-type: none;
  padding: 0;
}

h1,
h2,
h3 {
  margin: 0 0 1rem 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
  color: var(--white);
}

.background {
  overflow: hidden;
  height: 100vh;
  width: 100vw;
  position: fixed;
}

.single-item {
  width: 100%;
  z-index: 0;
}

img {
  width: 100%;
  height: 100%;
}

.noise {
  position: fixed;
  top: -50%;
  left: -50%;
  right: -50%;
  bottom: -50%;
  width: 200%;
  height: 200vh;
  background: transparent url("http://assets.iceable.com/img/noise-transparent.png") repeat 0 0;
  background-repeat: repeat;
  animation: bg-animation 0.2s infinite;
  opacity: 1;
  visibility: visible;
  z-index: 1;
  overflow: hidden;
}

@keyframes bg-animation {
  0% {
    transform: translate(0, 0);
  }
  10% {
    transform: translate(-5%, -5%);
  }
  20% {
    transform: translate(-10%, 5%);
  }
  30% {
    transform: translate(5%, -10%);
  }
  40% {
    transform: translate(-5%, 15%);
  }
  50% {
    transform: translate(-10%, 5%);
  }
  60% {
    transform: translate(15%, 0);
  }
  70% {
    transform: translate(0, 10%);
  }
  80% {
    transform: translate(-15%, 0);
  }
  90% {
    transform: translate(10%, 5%);
  }
  100% {
    transform: translate(5%, 0);
  }
}
.slick-slide {
  min-height: 100vh;
  object-fit: cover;
}

.main-logo {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%);
  z-index: 2;
}

.maincontent {
  transition: all 0.2s cubic-bezier(0.33, 1, 0.68, 1);
  opacity: 1;
}

.foot {
  position: fixed;
  width: 100%;
  bottom: 0;
  margin-bottom: 2rem;
  text-align: center;
}
.foot a:first-child {
  margin-right: 1.5rem;
}

.overlay {
  position: fixed;
  width: 100vw;
  height: 100vh;
  opacity: 0;
  display: block;
  z-index: 0;
  transition: all 0.8s cubic-bezier(0.33, 1, 0.68, 1);
  color: var(--white);
  overflow: auto;
  z-index: -1;
}
@media (max-width: 39.9375em) {
  .overlay {
    font-size: 0.75rem;
    letter-spacing: 0;
  }
}

.overlay--visible {
  opacity: 1;
  z-index: 5;
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
  background: rgba(0, 0, 0, 0.4);
  overflow-x: hidden;
}

.overlay-close {
  margin: 2rem;
  transition: all 0.4s cubic-bezier(0.33, 1, 0.68, 1);
  text-align: center;
}

.trailers {
  max-width: 1000px;
  margin: 0 auto;
}
@media (max-width: 63.9375em) {
  .trailers {
    margin: 1rem;
  }
}

.vid-embed {
  margin-bottom: 3rem;
}
.vid-embed p {
  margin-top: 2rem;
}

.flier {
  pointer-events: none;
}
.flier img {
  width: 300px;
  height: auto;
}

.flier > * {
  /* Adjust animation duration to change the element’s speed */
  animation: fly 75s linear infinite;
  pointer-events: none !important;
  top: 0;
  left: 0;
  transform: translateX(-120%) translateY(-120%) rotateZ(0);
  position: fixed;
  animation-delay: 1s;
  z-index: 999;
}

@keyframes fly {
  98.001%, 0% {
    display: block;
    transform: translateX(-200%) translateY(100vh) rotateZ(0deg);
  }
  15% {
    transform: translateX(100vw) translateY(-100%) rotateZ(180deg);
  }
  15.001%, 18% {
    transform: translateX(100vw) translateY(-30%) rotateZ(0deg);
  }
  40% {
    transform: translateX(-200%) translateY(3vh) rotateZ(-180deg);
  }
  40.001%, 43% {
    transform: translateX(-200%) translateY(-100%) rotateZ(-180deg);
  }
  65% {
    transform: translateX(100vw) translateY(50vh) rotateZ(0deg);
  }
  65.001%, 68% {
    transform: translateX(20vw) translateY(-200%) rotateZ(180deg);
  }
  95% {
    transform: translateX(10vw) translateY(100vh) rotateZ(0deg);
  }
}